.address__wrap {
	position: relative;
	padding: 56px 0;
}

.address__wrap h2 {
	margin: 0;
	font-weight: normal;
	color: rgb(28, 157, 229);
	text-align: center;
}

.address__wrap p {
	margin: 0 0 5px;
	font-size: 12px;
	text-align: center;
	line-height: 15px;
	color: rgba(160, 160, 160, 1);
}

.address__text-wrap {
	position: absolute;
	bottom: -197px;
	left: 0;
	right: 0;
	width: 257px;
	margin: 0 auto;
	padding: 170px 29px 21px;
	text-align: center;
	background-color: white;
	border-radius: 10px;
	background-image: url("../img/img-address-mob@1x.png");
	background-repeat: no-repeat;
	background-position: 8px 5px;
}

.address__text-wrap .address__span {
	font-size: 9px;
	color: rgb(160, 160, 160);
}

.address__text-wrap p {
	font-size: 15px;
	line-height: 14px;
	margin-bottom: 20px;
	color: rgba(41, 41, 43, 1);
}

.address__text-wrap p span {
	color: rgba(28, 157, 229, 1);
}

.address__btn {
	position: relative;
	border: 0;
	padding: 12px 30px;
	font-size: 13px;
	background: linear-gradient(266deg, red 0%, #ff2272 100%);
	box-shadow: 0 0 12px 3px rgba(255, 0, 0, 0.14);
	text-transform: none;
	color: white;
	z-index: 1;
	cursor: url("../img/icon-cursor.svg"), pointer;

	&::before {
		content: "";
		position: absolute;
		width: 211px;
		height: 56px;
		border: 1.5px solid #ffc6da;
		border-radius: 50px;
		top: -6px;
		left: -6px;
	}

	&:hover {
		background: linear-gradient(258deg, rgba(255, 57, 69, 1) 0%, rgba(255, 73, 150, 1) 100%);
		box-shadow: 0 0 7px 7px rgba(255, 0, 0, 0.14);
	}

	&:active {
		background: linear-gradient(280deg, rgba(221, 0, 0, 1) 0%, rgba(248, 0, 89, 1) 100%);
	}
}

.address__btn:hover::before {
	border: 2px solid rgba(255, 73, 150, 1);
}

.address__btn:active::before {
	border: 2px solid rgba(248, 0, 89, 1);
}

.address__map-wrap iframe {
	width: 100%;
}

@media only screen and (min-width: 768px) {
	.address__map-wrap iframe {
		width: 100%;
		height: 400px;
	}

	.address__text-wrap {
		bottom: 105px;
		left: 395px;
	}
}

@media only screen and (min-width: 1440px) {
	.address__wrap {
		padding: 159px 0 45px;
	}

	.address__wrap h2 {
		margin-bottom: 10px;
		font-size: 48px;
	}

	.address__wrap p {
		margin-bottom: 30px;
		font-size: 24px;
	}

	.address__map-wrap iframe {
		height: 661px;
	}

	.address__text-wrap {
		width: 412px;
		padding: 305px 40px 38px;
		right: -205px;
		bottom: 142px;
		background-image: url("../img/img-address@1x.png");
		background-position: 13px 29px;
	}

	.address__text-wrap p {
		line-height: 24px;
	}

	.address__text-wrap p span {
		color: black;
	}

	.address__text-wrap .address__span {
		font-size: 14px;
	}

	.address__btn {
		padding: 17px 39px;
		font-size: 17px;
		margin: 0 auto;

		&::before {
			width: 277px;
			height: 70px;
			top: -7px;
			left: -9px;
		}
	}
}
