.faq {
	overflow: hidden;
	background: linear-gradient(245deg, rgba(1, 112, 176, 1) 25%, rgba(24, 187, 235, 1) 100%);
}

.faq__wrap {
	position: relative;
	max-width: 320px;
	margin: 0 auto;
}

.faq__wrap-questions {
	padding: 25px 16px 12px;
}

.faq__wrap-questions h2 {
	margin: 0 0 8px;
	font-weight: normal;
	font-size: 20px;
	letter-spacing: 0.2px;
	color: white;
}

.faq__question {
	list-style: none;
	margin: 0;
	padding: 0;
}

.faq__question-item {
	position: relative;
	background-color: white;
	border-radius: 5px;
	padding: 9px 35px 9px 11px;
	margin-bottom: 9px;
	background-image: url('../img/icon-arrow-down.svg');
	background-repeat: no-repeat;
	background-size: 17px 17px;
	background-position: 259px 12px;
	cursor: url('../img/icon-cursor.svg'), pointer;
}

.faq__question-item--up {
	background-image: url('../img/icon-arrow-up.svg');
	background-repeat: no-repeat;
	background-size: 17px 17px;
	background-position: 259px 12px;
}

.faq__question-item h3 {
	margin: 0;
	font-size: 12px;
	font-weight: normal;

	&:active {
		color: rgba(1, 112, 176, 1);
	}

	&:hover {
		background: linear-gradient(355deg, rgba(1, 112, 176, 1) 28%, rgba(24, 187, 235, 1) 100%);
		-webkit-background-clip: text; /*stylelint-disable-line*/
		-webkit-text-fill-color: transparent;
		cursor: url('../img/icon-cursor.svg'), pointer;
	}
}

.faq__question-content {
	display: none;
}

.faq__question-content .faq__question-br {
	display: none;
}

.faq__question-content p {
	font-size: 9px;
	margin: 0;
	line-height: 14px;
	max-width: 232px;
}

@media only screen and (min-width: 768px) {
 .faq__wrap {
		max-width: 768px;
		display: flex;
		padding: 20px 30px;
	}

	.faq__wrap-questions {
		padding: 25px 30px 12px 30px;
		margin-right: 20px;
	}
}

@media only screen and (min-width: 1440px) {
	.faq__wrap {
		max-width: 1200px;
		padding: 32px 0 37px;
	}

	.faq__wrap-questions {
		margin-right: 108px;
		padding: 24px 24px 24px 14px;
	}

	.faq__wrap-questions h2 {
		font-size: 36px;
		margin: 0 0 25px;
	}

	.faq__question-item {
		padding: 24px 83px 15px 22px;
		margin-bottom: 15px;

		&::before {
			right: 18px;
			top: 20px;
			width: 30px;
			height: 30px;
			background-size: 30px 30px;
		}
	}

	.faq__question-item h3 {
		margin-bottom: 8px;
		font-size: 20px;
	}

	.faq__question-content p {
		font-size: 14px;
		max-width: 400px;
		line-height: 21px;
		letter-spacing: -0.13px;
	}

	.faq__question-content--show {
		display: none;
	}

	.faq__question-content p br {
		display: none;
	}

	.faq__question-content .faq__question-br {
		display: block;
	}

	.faq__question-item {
		background-image: url('../img/icon-arrow-down.svg');
		background-repeat: no-repeat;
		background-size: 30px 30px;
		background-position: 460px 20px;
	}

	.faq__question-item--up {
		background-image: url('../img/icon-arrow-up.svg');
		background-repeat: no-repeat;
		background-size: 30px 30px;
		background-position: 460px 20px;
	}
}
